
import { Component } from 'react';
import { findDOMNode } from 'react-dom'
import { css } from 'emotion'
import Link from 'next/link'
import { Button } from 'reactism/elements'

import MainGrid  from 'reactism/components/containers/gridLayouts/mainGrid'
import ListGrid  from 'reactism/components/containers/gridLayouts/listGrid'

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displayProduct : false,
      displayKnowMore : false,
      toogleNav : true,
    }
  }
  componentDidUpdate() {
    document.addEventListener('click', this.handleDocumentClick, false)
    document.addEventListener('touchend', this.handleDocumentClick, false)
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleDocumentClick, false)
    document.removeEventListener('touchend', this.handleDocumentClick, false)
  }

  handleDocumentClick = e => {
    if (this) {
      if (!findDOMNode(this).contains(e.target)) {
        this.setState({ toogleNav : true })
      }
    }
  }

  handleToggle = value => {
    const { displayProduct, displayKnowMore } = this.state;

    if (value === 'displayProduct') {
      // setDisplayProduct(!displayProduct)
      this.setState({ displayProduct: !displayProduct})
      displayKnowMore === true && this.setState({ displayKnowMore : !displayKnowMore })
    }
    if (value === 'displayKnowMore') {
      this.setState({ displayKnowMore : !displayKnowMore })
      displayProduct === true && this.setState({ displayProduct: !displayProduct})
    }
  }

  render() {
    const { handleToggle } = this;
    const { styleColorTextHeader } = this.props;
    const { toogleNav, displayProduct, displayKnowMore } = this.state;

    const mainStyle = css`
      @import url('https://fonts.googleapis.com/css?family=Ubuntu:400,400i,700,700i');
      font-family: 'Ubuntu', sans-serif;
      position: absolute;
      z-index: 1;
      background-color: transparent;
      box-shadow: 0px -9px 25px 0px rgba(0,0,0,0.8);
      width: 100%;
      margin: -8px;
      ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        overflow: hidden;
        li {
          float: left;
          margin-top: 10px;
          a {
            cursor: pointer;
            text-decoration: none;
            padding: 14px;
            ${styleColorTextHeader ||`color: #000 `};
            font-size: 0.9rem;
            span img {
              margin-bottom: -6px;
            }
          }
        }
      }
      .sub-menu{
        overflow: inherit;
        margin: -30px 0px -30px -14px;
        height: 70px;
        padding: 10px 0px;
        li a {
          padding: 10px;
          span{
            margin-right:10px;
          }
        }
      }
      .hamburger {
        cursor: pointer;
        width: 10%;
        float: right;
        transition: 0.5s;
        div {
          background-color: #000;
          height: 2px;
          margin: 6px 0px;
          transition: 0.5s;
        }
      }
      .cross {
        div:nth-child(1) {
          transform: rotate(-45deg);
          margin-top: 13px;
          transition: 0.5s;
        }
        div:nth-child(2) {
          transform: rotate(45deg);
          margin-top: -6px;
          transition: 0.5s;
        }
        div:nth-child(3) {
          transform: rotate(45deg);
          margin-top: -6px;
          transition: 0.5s;
        }
        width: 10%;
        float: right;
        div {
          background-color: #000;
          height: 2px;
          margin: 4px 0px;
        }
        transition: 0.5s;
      }
      .sub-menu-main-600 {
        display: none;
      }
      .responsive {
        display: block;
        transition: all 0.5s linear;
      }
      .hamburger, .cross {
        display: none;
      }
      @media only screen and (max-width: 600px) {
        color: #000;
        background-color: #fff;
        margin: 0px;
        ul {
          li {
            width: 100%;
            margin: 10px 0px;
          }
        }
        .cross {
          div:nth-child(2) {
            height: 0px;
            transition: 0.1s;
          }
        }
        .display-important-600 {
          display: none;
        }
        .sub-menu-main-600 {
          display: block;
        }
        .sub-menu {
          margin: 0px;
        }
        .responsive {
          display: none;
          transition: all 0.5s linear;
        }
        .hamburger, .cross {
          display: block;
        }
      }
    `
    const listGridStyle = css`
      padding-bottom: 10px;
    `
    const buttonTranparent = css`
      font-family: 'Ubuntu', sans-serif;
      color: #1798D1;
      border: 1px solid #1798D1;
      font-size: 1rem;
      background-color: transparent;
      font-weight: 100;
      padding: 10px 15px;
    `
    const buttonFilled = css`
      font-family: 'Ubuntu', sans-serif;
      color: #fff;
      background-color: #1798D1;
      font-weight: 100;
      padding: 10px 15px;
    `

    // const [displayProduct, setDisplayProduct] = useState(false);
    // const [displayKnowMore, setDisplayKnowMore] = useState(false);
    // const [toogleNav, setToogleNav] = useState(true);

    // const handleToggle = value => {
    //   if (value === 'displayProduct') {
    //     setDisplayProduct(!displayProduct)
    //     displayKnowMore === true && setDisplayKnowMore(!displayKnowMore)
    //   }
    //   if (value === 'displayKnowMore') {
    //     setDisplayKnowMore(!displayKnowMore)
    //     displayProduct === true && setDisplayProduct(!displayProduct)
    //   }
    // }

    return (
      <div className={ mainStyle }>
      <MainGrid width={80}>
        <ListGrid cols={3} style={ listGridStyle }>
          <div>
            <div className={ toogleNav ? 'hamburger' : 'cross' } onClick={ () => this.setState({ toogleNav: !toogleNav})}>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
          <Link href="/">
            <a>
              <img src="/static/docob_logo.svg" alt="docoblast-logo" width={100}/>
            </a>
          </Link>
        </ListGrid>
        <div className={ toogleNav ? 'responsive' : '' }>
          <ListGrid cols={15}>
            <ul>
              <li>
                <a onClick={() => handleToggle('displayProduct')}>
                  <span>Produk</span>
                  <span>
                    <img src={ displayProduct ? "/static/ic_up.svg" : "/static/ic_down.svg" } alt="icons" width={20}/>
                  </span>
                </a>
                <div className="sub-menu-main-600">
                  <ul className={`sub-menu ${ displayProduct ? css`display:block;` : css`display:none;` }`}>
                    <li>
                      <Link href="/products">
                        <a><span><img src="/static/ic_produk_docoblast.svg" alt="icons-down" width={25}/></span><span>DocoBlast</span></a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/products/why">
                        <a><span><img src="/static/ic_produk_mengapa.svg" alt="icons-down" width={25}/></span>Mengapa DocoBlast?</a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/products/benefit">
                        <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Manfaat</a>
                      </Link>
                    </li>
                  </ul>
                </div>
              </li>
              <li>
                <Link href="/features"><a>Fitur</a></Link>
              </li>
              <li>
                <Link href="/price"><a>Harga</a></Link>
              </li>
              <li>
                <a onClick={ () => handleToggle('displayKnowMore') }>
                  <span>Ketahui Lebih</span>
                  <span>
                    <img src={ displayKnowMore ? "/static/ic_up.svg" : "/static/ic_down.svg" } alt="icons" width={20}/>
                  </span>
                </a>
                <div className="sub-menu-main-600">
                  <ul className={`sub-menu  ${ displayKnowMore ? css`display:block;` : css`display:none;` }`}>
                    <li>
                      <Link href="/information">
                        <a><span><img src="/static/ic_produk_docoblast.svg" alt="icons-down" width={25}/></span><span>Panduan</span></a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/information/customers">
                        <a><span><img src="/static/ic_produk_mengapa.svg" alt="icons-down" width={25}/></span>Pelanggan Kami</a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/information/resource">
                        <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Sumber Daya</a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/information/team">
                        <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Tim DocoBlast</a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/information/ourApi">
                        <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>DocoBlast API</a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/information/contact">
                        <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Kontak Kami</a>
                      </Link>
                    </li>
                  </ul>
                </div>
              </li>
              <li>
                <Link href="/blogs"><a>Blog</a></Link>
              </li>
            </ul>
          </ListGrid>
          <ListGrid cols={6}>
            <div className="responsive">
              <Link href="/">
                <Button style={ buttonTranparent }>Masuk</Button>
              </Link>
              <Link href="/">
                <Button style={ buttonFilled }>Coba Gratis</Button>
              </Link>
            </div>
          </ListGrid>
        </div>
      </MainGrid>
      <div className="display-important-600">
        <MainGrid width={80} display={displayProduct ? 'block' : 'none'}>
          <ListGrid cols={12}>
            <ul className="sub-menu">
              <li>
                <Link href="/products">
                  <a><span><img src="/static/ic_produk_docoblast.svg" alt="icons-down" width={25}/></span><span>DocoBlast</span></a>
                </Link>
              </li>
              <li>
                <Link href="/products/why">
                  <a><span><img src="/static/ic_produk_mengapa.svg" alt="icons-down" width={25}/></span>Mengapa DocoBlast?</a>
                </Link>
              </li>
              <li>
                <Link href="/products/benefit">
                  <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Manfaat</a>
                </Link>
              </li>
            </ul>
          </ListGrid>
        </MainGrid>
        <MainGrid width={80} display={displayKnowMore ? 'block' : 'none'}>
          <ListGrid cols={24}>
            <ul className="sub-menu">
              <li>
                <Link href="/information">
                  <a><span><img src="/static/ic_produk_docoblast.svg" alt="icons-down" width={25}/></span><span>Panduan</span></a>
                </Link>
              </li>
              <li>
                <Link href="/information/customers">
                  <a><span><img src="/static/ic_produk_mengapa.svg" alt="icons-down" width={25}/></span>Pelanggan Kami</a>
                </Link>
              </li>
              <li>
                <Link href="/information/resource">
                  <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Sumber Daya</a>
                </Link>
              </li>
              <li>
                <Link href="/information/team">
                  <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Tim DocoBlast</a>
                </Link>
              </li>
              <li>
                <Link href="/information/ourApi">
                  <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>DocoBlast API</a>
                </Link>
              </li>
              <li>
                <Link href="/information/contact">
                  <a><span><img src="/static/ic_produk_manfaat.svg" alt="icons-down" width={25}/></span>Kontak Kami</a>
                </Link>
              </li>
            </ul>
          </ListGrid>
        </MainGrid>
      </div>
      </div>
    )
  }
}

export default Index;
